import { useContext, useState } from "react";
import "./profileUpdatePage.scss";
import { AuthContext } from "../../context/AuthContext";
// import apiRequest from "../../lib/apiRequest";
import { useNavigate } from "react-router-dom";
import { userRequest } from "../../lib/apiRequest";
import UploadWidget from "../../components/uploadWidget/UploadWidget";

function ProfileUpdatePage() {
  const { currentUser, updateUser } = useContext(AuthContext);

  const navigate = useNavigate();
  const [error,setError] = useState('');
  const [avatar,setAvatar] = useState([]);
  const handleSubmit = async(e) =>{
    e.preventDefault();
    const formData = new FormData(e.target);

    const {username,email,password} = Object.fromEntries(formData);
    try{
      const axiosInstance = userRequest(currentUser?.accessToken)
      const res = await axiosInstance.put(`/user/${currentUser.id}`,{
        username,
        email,
        password,
        avatar:avatar[0]
      })
      updateUser(res.data);
      navigate('//profile')
    }catch(err){
      console.log(err);
      setError(err.response.data.message);
    }
  }
  return (
    <div className="profileUpdatePage">
      <div className="formContainer">
        <form onSubmit={handleSubmit} >
          <h1>Update Profile</h1>
          <div className="item">
            <label htmlFor="username">Username</label>
            <input
              id="username"
              name="username"
              type="text"
              defaultValue={currentUser.userInfo.username}
            />
          </div>
          <div className="item">
            <label htmlFor="email">Email</label>
            <input
              id="email"
              name="email"
              type="email"
              defaultValue={currentUser.userInfo.email}
            />
          </div>
          <div className="item">
            <label htmlFor="password">Password</label>
            <input id="password" name="password" type="password" />
          </div>
          <button>Update</button>userInfo.
          {error && <span>error</span>}
        </form>
      </div>
      <div className="sideContainer">
        <img src={avatar[0]||currentUser.userInfo.avatar || "/noavatar.jpg"} alt="" className="avatar" />
        <UploadWidget uwConfig={{
          cloudName : 'dxhyjkbgx',
          uploadPreset:"estate",
          multiple:false,
          maxImageFileSize:2000000,
          folder:'avatars'
        }}
        setState={setAvatar}/>
      </div>
    </div>
  );
}

export default ProfileUpdatePage;